class Form extends Component {
    constructor(props) {
        super(props);
        this.time = {
            "Second": 60
        }
        this.amount=[]
        // this.form = {}
        this.form_names = ['invoiceTitle', 'invoiceAmount', 'invoiceContent', 'phone','taxNumber','info1','email']
        this.error = {
            "text": "sure"
        }
        this.phone =[]
        this.selfphone=''
        this.info1 = [{value: '01', text: "企业"}, {value: '02', text: '机关单位'},{value: '03', text: '个人'},{value: '04', text: '其他'}]

        this.default_select = (...args) => {
            for (let i in args) {
                let arg = args[i];
                if (arg in form) {
                    let checked = form[arg].split(',');
                    for (let j in checked) {
                        if (checked[j] !== '') {
                            $('input[name="' + arg + '"][value="' + checked[j] + '"]').prop('checked', 'checked');
                        }
                    }
                }
            }
        }
    }

    componentDidMount() {
        $.get(Constant.url + 'queryInvoiceAmount.do',(data)=>{
            if(data.error_no == '0'){
                this.amount=data
                this.setState(this.amount);
            }
        });
        // $.get(Constant.url + 'hasLoginBaidu.do',(data)=>{
        //     if(data.error_no == '0') {
        //
        //         if(data.data.phone == undefined){
        //
        //         }else{
        //             this.selfphone=data.data.phone
        //             this.phone = data.data.phone
        //             this.setState({
        //                     phone:this.phone,
        //                     selfphone:this.selfphone,
        //                 }
        //             )
        //             form['phone']=this.phone
        //             this.setState(form)
        //         }
        //     }
        // });
        $.get(Constant.url + 'queryInvoiceInfo.do',(data)=>{
            if(data.error_no ==0 && data.data!==undefined){
                console.log(form)
                form = data.data;
                // this.setState(form)
                this.setState({
                    form: form
                });
                if ("info1" in form) {
                    $('#info1').val(form['info1']);
                }
                this.default_select('info1')

            }
        })
    }
    submit = () => {
        let flag = true;
        let _self = this;
        let amount = _self.refs['invoiceAmount'].props.amount
        form.invoiceAmount = amount
        _self.form_names.every((i)=>{
            if (!_self.refs[i].valid()) {
                flag = false
                return false
            }
        })
        form.invoiceType=0;
        if(flag) {
            form['createDate']= new Date();//去空用,
            $.post(Constant.url + 'saveInvoice.do',form,(data)=>{
                if(data.error_no == '0') {
                    //TODO success call back
                    this.error.text = 'noError'
                    this.setState(this.error)
                    this.refs.Menu.open()
                }
                else {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
            });
        }
    }
    render() {
        return <div>
            <Title>
                <FormattedMessage id="invoice_info_title"/>
            </Title>
            <Menu ref="Menu" text={this.error.text}/>
            <table className={IsPcOrNot.get() ? "form-table" : "wap-table"}>
                <tbody >
                <tr>
                    {
                        IsPcOrNot.get()?<td style={{paddingRight:'150px',paddingTop:'80px',textAlign:'left'}}>
                            <FormattedMessage id="invoice_info"/>
                        </td>:null

                    }
                    <td style={{padding:IsPcOrNot.get()?"80px 0 30px 0":''}}>
                        <SelectLine form={form} ref={this.form_names[5]} text="info1" data={this.info1}
                                    name={this.form_names[5]} valid="notNull"/>
                        <FormLine
                            ref={this.form_names[0]} icon={invoiceTitle}
                            text="invoiceTitle"
                            name={this.form_names[0]}
                            valid="notNull"/>
                        <FormLine
                            ref={this.form_names[4]} icon={hamount}
                            text="taxNumber"
                            name={this.form_names[4]}
                            valid="notNull"
                        />
                        <div style={{fontSize:'12px',textAlign:IsPcOrNot.get()?'left':'center',padding:'15px 0',color:'#999',width:IsPcOrNot.get()?'600px':''}}>
                            <FormattedMessage id="user_id_info"/>
                        </div>
                            <div style={{textAlign: "left", verticalAlign: "middle"}}>
                            <FormLine
                                ref={this.form_names[1]}
                                text="invoiceAmount"
                                icon={amount}
                                name={this.form_names[1]}
                                valid="notNull"
                                amount={this.amount.data}
                            />
                        </div>
                        <FormLine ref={this.form_names[2]} icon={invoiceContent} text="invoiceContent" name={this.form_names[2]}
                                  valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    {
                        IsPcOrNot.get()?<td style={{paddingRight:'150px',paddingTop:'80px',textAlign:'left'}}>
                        </td>:null

                    }
                    <td>
                        <div style={{fontSize:'12px',textAlign:IsPcOrNot.get()?'left':'left',padding:'15px 0',color:'#999',width:IsPcOrNot.get()?'600px':''}}>
                            <FormattedMessage id="receive_user_info"/>
                        </div>
                        <FormLine ref={this.form_names[3]} icon={PhoneIcon} text="x_phone" name={this.form_names[3]}
                                  valid="notNull" phone={this.phone}/>
                        <FormLine ref={this.form_names[6]} icon={Email} text="email" name={this.form_names[6]}
                                  valid="notNull"/>
                    </td>
                </tr>
                </tbody>
            </table>
            <div style={{width: '5.63rem',fontSize:'14px', color:' rgb(102, 102, 102)',margin:IsPcOrNot.get()?"":'0 auto'}}>
                <FormattedMessage id="pcinvoice_tip"/>
                <span style={{color:'red'}}>
                <FormattedMessage id="cinvoice_tip2"/>
            </span>
                <FormattedMessage id="pcinvoice_tip2"/>
            </div>
            <div style={{margin: "10px 0 60px 0", textAlign: "center"}}>
                <button type="submit" className="base_type_btn"
                        style={{padding: "15px 80px", borderRadius: "3px", marginTop: "40px"}} onClick={this.submit}>
                    <FormattedMessage id="submit"/>
                </button>
            </div>
        </div>

    }
}

class Phone extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <Form/>
            <Footer/>
        </div>
    }
}

class PC extends Component {
    render() {
        return <div style={{backgroundColor: "#ffffff"}}>
            <Header/>
            <div style={{margin: "0 auto", display: "block", position: "relative", width: "1200px"}}>
                <Form/>
            </div>
            <Footer/>
        </div>
    }
}

export class Invitation extends Component {
    constructor(props) {
        super(props);
    }

    changeLanguage = () => {
        if (localStorage['language'] == 'en') {
            localStorage['language'] = 'zh-CN';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };

    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}

export default Invitation



